<template>
  <div id="app-blog">
    <h2>添加博客</h2>
    <form v-if="!submmited">
        <label>博客标题</label>
        <input type="text" v-model="blog.title" required />
        <br>
        <label>博客内容</label>
        <textarea v-model="blog.content"></textarea>

        <div id="checkboxes">
            <label>Vue.js</label>
            <input type="checkbox" value="Vue.js" v-model="blog.categories">
            <label>Node.js</label>
            <input type="checkbox" value="Node.js" v-model="blog.categories">
            <label>React.js</label>
            <input type="checkbox" value="React.js" v-model="blog.categories">
            <label>Angular.js</label>
            <input type="checkbox" value="Angular.js" v-model="blog.categories">
        </div>

        <label>作者：</label>
        <select v-model="blog.author">
        <option v-for="author in authors">
        {{author}}
        </option>
        </select>

        <button v-on:click.prevent="post">添加博客</button>
    </form>

    <div v-if="submmited">
      <h3>你的博客添加完成</h3>
    </div>
    <hr>

    <div id="preview">
        <h3>博客总览</h3>
        <p>博客标题：{{blog.title}}</p>
        <p>博客内容</p>
        <p>{{blog.content}}</p>
        <p>博客分类</p>
        <ul>
          <li v-for="category in blog.categories">
            {{category}}
          </li>
        </ul>
        <p>作者：{{blog.author}}</p>

    </div>

    

  </div>
</template>

<script>
export default {
  //http://jsonplaceholder.typicode.com/posts       post地址

  name: 'app-blog',
  data () {
    return {
        blog:{
            title:" ",
            content:" ",
            categories:[],
            author:" ",
        },
        authors:["herry","jams","dany"],
        submmited:false,
    }
  },
  methods:{
    post:function(){
      this.$http.post("https://vue-demone.firebaseio.com/posts.json",this.blog)
      .then(function(data){
        console.log(data);
        this.submmited = true;
      });
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #app-blog *{
    box-sizing: border-box;
   }
  #app-blog{
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
  }
  label{
    display: block;
    margin: 20px 0 10px;
  }
  input[type="text"],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
  }
  textarea{
    height: 230px;    
  }
  #checkboxes label{
    display: inline-block;
    margin-top: 10px;
  }
  #checkboxes input{
    display: inline-block;
    margin-right: 10px;
  }
  button{
    display: block;
    margin: 20px 0;
    background: orangered;
    padding: 10px;
    font-size: 17px;
    color: wheat;
    border-radius: 5px;
    cursor: pointer;
  }
  #preview{
    padding: 10px 20px;
    border: 2px dotted rgb(114, 8, 8);
    margin: 30px;
  }
  h3{
    margin-top: 10px;
  }
</style>



